import 'ka-table/style.css';
import React, { useState,useEffect } from 'react';

import { kaReducer, Table, useTable } from 'ka-table';
import { kaPropsUtils } from 'ka-table/utils';
import { DataType, SortDirection, SortingMode } from 'ka-table/enums';
import { min, select } from 'd3';


const SortingTable = (props) => {

  const [selectedData, changeSelectedData] = useState();
  const table = useTable({
    onDispatch: (action, tableProps) => {
      const selected = kaPropsUtils.getSelectedData(tableProps).pop();

      changeSelectedData(selected);
      if(selected.word){
        props.sendSelectedPath(selected.word);
      }
    },
  });


  useEffect(() => {

    const handler = () => table.deselectAllRows();;

    window.addEventListener("click", handler);
    return () => { window.removeEventListener("click", handler); };
  });
  

  const dispatch = (action) => {
    changeTableProps((prevState) => kaReducer(prevState, action));
  };

  const data = props.data.map(object => {
    let selected_months = Object.entries(object.months).slice(props.start,min([props.end+1,props.start+5]));
    selected_months = selected_months.reduce((total,cur)=>({ ...total,[cur[0]]:cur[1]?cur[1].toFixed(2):cur[1]}),{});
    return { word: object.word, group: object.group, ...selected_months };
  });

  const monthly_frequency = Object.keys(props.data[0].months).slice(props.start,min([props.end+1,props.start+5])).map((month) => {
    return {
      dataType: DataType.Number,
      key: month,
      style: { width: 90 },
      isSortable: false,
      title: month,
    }
  });


  const tablePropsInit = {

    columns: [
      {
        dataType: DataType.String,
        key: 'word',
        style: { width: 90 },
        title: 'word',
      },
      {
        dataType: DataType.String,
        key: 'group',
        style: { width: 90 },
        isSortable: false,
        title: 'group',
      },
      ...monthly_frequency
    ],
    data: data,
    rowKeyField: 'word',
    sortingMode: SortingMode.Single,
  };

  const [tableProps, changeTableProps] = useState(tablePropsInit);


  return (
    <div>
    <Table
      table={table}
      {...tableProps}
      childComponents= {{
        dataRow:{
          elementAttributes: () => ({
            onClick: (event,extendedEvent) => {
              event.stopPropagation();
              table.selectSingleRow(extendedEvent.childProps.rowKeyValue);
            }
          }),
        },
      }}
    />
    </div>
  );
};

export default SortingTable;

