import './App.css';
import { useState, useEffect } from 'react';
// import createParallel from './components/Parellel';
import data from './data/parallel_coordinates.json';
import Parallel from './components/Parallel';
import Dropdown from './components/Dropdown';
import SortingTable from './components/Table';

function getKeyIndex(arr, k) {
  let idx = arr.findIndex((element) => element === k);
  return idx;
}

function App() {

  let months = Object.keys(data[0]["months"]);

  const [start_range, setStart] = useState(months[0]);
  const [end_range, setEnd] = useState(months[(months.length < 5 ? months.length - 1 : 4)]);

  const [selected_path, setSelectedPath] = useState();

  return (
    <div className="App">
      <h2>Smoking Cessation Related Tweets: Monthly Word Frequency Dynamic Parallel Coordinates</h2>
      <div className="layout">
      <div className="table-container">
        <div className="table">
          <SortingTable key={[start_range, end_range].join(' ')}
            sendSelectedPath={(id) => { setSelectedPath(id) }}
            start={getKeyIndex(months, start_range)}
            end={getKeyIndex(months, end_range)}
            data={data} />
        </div>
      </div>
      <div className="parallel-container">
        <div className="menu-container">
          <Dropdown isStart options={months} sendOption={(option) => { setStart(option) }} default={start_range} />
          <Dropdown isEnd options={months} sendOption={(option) => { setEnd(option) }} default={end_range} />
        </div>
        <Parallel width={window.innerWidth / 2}
          height={window.innerHeight / 1.2}
          data={data}
          selected_path={selected_path}
          key={[start_range, end_range].join(' ')}
          start={getKeyIndex(months, start_range)}
          end={getKeyIndex(months, end_range)}
          range={months.slice(getKeyIndex(months, start_range), getKeyIndex(months, end_range) + 1)} />
      </div>
      </div>
    </div>
  );
}

export default App;
