import React, { useState, useEffect } from 'react';
import RightArrow from '../icon/RightArrow';
import "./Dropdown.css";

function Dropdown(props) {



    // Hooks enable us to use state without wrting a class 
    const [showMenu, setShowMenu] = useState(false);
    const [selectedValue, setSelectedValue] = useState(props.default);

    useEffect(() => {

        const handler = () => setShowMenu(false);

        window.addEventListener("click", handler);
        return () => { window.removeEventListener("click", handler); };
    });

    const handleMenuClick = (e) => {
        e.stopPropagation();
        setShowMenu(!showMenu);
    }

    const handleOptionClick = (option) => {
        setSelectedValue(option);
        props.sendOption(option);
    }

    return (
        <div className="dropdown_container">
            <div className="dropdown_input" onClick={handleMenuClick}>{selectedValue}<RightArrow width='20%'/></div>
            {showMenu &&
                (<div className="dropdown_menu">
                    {props.options.map((o) => (
                        <div className="dropdown_option" key={o} onClick={() => handleOptionClick(o)}>{o}
                        </div>
                    ))
                    }
                </div>)
            }
        </div>
        
        
    )

}


export default Dropdown;