import React, { Component } from 'react';
import * as d3 from "d3";

import "./Parallel.css";

var color = d3.scaleOrdinal(["#4e79a7", "#f28e2c", "#e15759", "#76b7b2", "#59a14f", "#edc949", "#af7aa1", "#ff9da7", "#9c755f", "#bab0ab"]);

let unhighlight = () => {
    console.log("dfdf");
    d3.selectAll(".line")
    .transition().duration(200).delay(100)
    .style("stroke", function (d) { return (color(d.group)) })
    .style("opacity", "1")
};

class Parallel extends Component {

    componentDidMount() {
        this.drawParellel();
    
        window.addEventListener("click", unhighlight);
    };

    componentDidUpdate(previousProps) {
        if (previousProps.selected_path !== this.props.selected_path) {
            d3.selectAll(".line")
                .transition().duration(200)
                .style("stroke", "lightgrey")
                .style("opacity", "0.2")
            // Second the hovered specie takes its color
            d3.selectAll(`#${this.props.selected_path}`)
                .transition().duration(200)
                .style("stroke", function (d) { return (color(d.group)) })
                .style("opacity", "1")
        }
    }

    // returnLinearScale = new Map(Array.from(keys, key => [key, d3.scaleLinear(d3.extent(data, d => d[key]), [margin.left, width - margin.right])]));

    // returnPointScale = d3.scalePoint(keys, [margin.top, height - margin.bottom])

    drawParellel() {

        // set the dimensions and margins of the graph
        var margin = { top: 30, right: 50, bottom: 10, left: 50 },
            width = this.props.width - margin.left - margin.right,
            height = this.props.height - margin.top - margin.bottom;

        // append the svg object to the body of the page
        // render method comes before componentDidMount
        var svg = d3.select("#parallel")
            .append("svg")
            .attr("width", width + margin.left + margin.right)
            .attr("height", height + margin.top + margin.bottom)
            .append("g")
            .attr("transform",
                "translate(" + margin.left + "," + margin.top + ")");

        var div = d3.select("body").append("div")
            .attr("class", "tooltip")
            .style("opacity", 0);
        // Parse the Data

        // Here I set the list of dimension manually to control the order of axis:
        let months = Object.keys(this.props.data[0]["months"]);
        var dimensions = months.slice(this.props.start, this.props.end + 1);

        // // For each dimension, I build a linear scale. I store all in a y object

        var y = d3.scaleLinear()
            .range([height, 0])
            .domain(d3.extent(this.props.data.reduce((total, cur) => [...total, ...Object.values(cur.months)], [])), d => d);


        // Build the X scale -> it find the best position for each Y axis

        var x = d3.scalePoint()
            .range([0, width])
            .domain(dimensions);

        // Highlight the specie that is hovered
        var highlight = function (d) {

            let selected_group = d.group;
            // first every group turns grey
            d3.selectAll(".line")
                .transition().duration(200)
                .style("stroke", "lightgrey")
                .style("opacity", "0.2")
            // Second the hovered specie takes its color
            d3.selectAll("." + selected_group)
                .transition().duration(200)
                .style("stroke", color(selected_group))
                .style("opacity", "1")
        }

        // Unhighlight
        var doNotHighlight = function (d) {
            d3.selectAll(".line")
                .transition().duration(200).delay(1000)
                .style("stroke", function (d) { return (color(d.group)) })
                .style("opacity", "1")
        }

        // The path function take a row of the csv as input, and return x and y coordinates of the line to draw for this raw.
        // d[p] 
        function path(d) {

            return d3.line()(dimensions.map(function (p) { return [x(p), y(d.months[p])]; }));
        }

        // Draw the lines
        svg
            .selectAll("myPath")
            .data(this.props.data)
            .enter()
            .append("path")
            .attr("class", function (d) { return "line " + d.group }) // 2 class for each line: 'line' and the group name
            .attr("d", path)
            .attr("id", d => d.word)
            .style("fill", "none")
            .style("stroke", function (d) { return (color(d.group)) })
            .style("stroke-width", 3)
            .style("opacity", 0.5)
            .on("mouseover", function (event, d) {
                highlight(d);
                div.transition()
                    .duration(250)
                    .style("opacity", 1);
                div.html(d.word).style("left", (event.pageX) + "px").style("top", (event.pageY - 30) + "px");
            })
            .on("mouseleave", function (event, d) {
                doNotHighlight(d);
                div.transition()
                    .duration(250)
                    .style("opacity", 0);
            });

        // Draw axis:
        svg.selectAll("myAxis")
            // For each dimension of the dataset I add a 'g' element:
            .data(dimensions).enter()
            .append("g")
            .attr("class", "axis")
            // I translate this element to its right position on the x axis
            .attr("transform", function (d) { return "translate(" + x(d) + ")"; })
            // And I build the axis with the call function
            .each(function (d) { d3.select(this).call(d3.axisLeft().ticks(9).scale(y)); })
            .style("stroke-width", 1.5)
            // Add axis title
            .append("text")
            .style("text-anchor", "middle")
            .attr("y", -9)
            .text(function (d) { return d; })
            .style("fill", "black")
    };

    render() {
        return <div id="parallel"></div>
    }

};

export default Parallel;

